/*eslint camelcase: ['error', {'properties': 'never'}]*/

import {StateMachine} from "./statemachine"

var dbglevel: number = (window as any).instrustat.debuglevel

export function cppcommCheckForUserStartline( that: StateMachine ) {
    (window as any).iface.setFlag( 'bottom', 'gotusrsl' )
}

export function cppcommNoUserStartline( that: StateMachine ) {
    that.gotusrsl = false
}

export function cppcommUserStartline( that: StateMachine ) {
    that.gotusrsl = true
}
